import React, { useState } from 'react';
import {
    DesktopOutlined,
    FileOutlined,
    PieChartOutlined,
    TeamOutlined,
    UserOutlined,
} from '@ant-design/icons';
import type { MenuProps } from 'antd';
import { Menu } from 'antd';
import { useNavigate, useLocation } from 'react-router-dom';

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
    label: React.ReactNode,
    key: React.Key,
    icon?: React.ReactNode,
    children?: MenuItem[],
): MenuItem {
    return {
        key,
        icon,
        children,
        label,
    } as MenuItem;
}

const items: MenuItem[] = [
    getItem('Option 1', '1', <PieChartOutlined />),
    getItem('Option 2', '2', <DesktopOutlined />),
    getItem('User', 'sub1', <UserOutlined />, [
        getItem('Tom', '3'),
        getItem('Bill', '4'),
        getItem('Alex', '5'),
    ]),
    getItem('Team', 'sub2', <TeamOutlined />, [getItem('Team 1', '6'), getItem('Team 2', '8')]),
    getItem('Files', '9', <FileOutlined />),
];

const MainMenu: React.FC = () => {
    const navigateTo = useNavigate();
    let firstOpenKey: string = '';
    const [openKeys, setOpenKeys] = useState([firstOpenKey]);
    const currentRoute = useLocation();
    function findKey(obj:{key:string}) {
        return obj.key === currentRoute.pathname;
    }
    for (let i = 0; i < items.length; i++) {
        if (items[i]!['children']&&items[i]!['children'].length>1&&items[i]!['children'].find(findKey)) {
            firstOpenKey = items[i]!.key as string;
            break;
        }
    }

    const menuClick = (e: { key: string }) => {
        navigateTo(e.key);
    };

    const handleOpenChange = (keys: string[]) => {
        setOpenKeys([keys[keys.length - 1]]);
    };

    return (<Menu theme="dark" defaultSelectedKeys={[currentRoute.pathname]} mode="inline" items={items} onClick={menuClick} onOpenChange={handleOpenChange} openKeys={openKeys} />);
};

export default MainMenu;